html {
  height: 100%;
}
body {
  background-color: #f7f8fA;
  color: #6a7884;
  font-family: 'Ubuntu', sans-serif;
  font-size: 14pt;
  font-weight: 400;
  margin: 0;
  min-height: 100%;
  display: -webkit-flex;
  display: flex;
  flex-direction: column;
  flex: auto;
}
a {
  text-decoration: none;
}
h1,
h2 {
  color: #6a7884;
  font-family: 'Vollkorn', serif;
  font-size: 28pt;
  font-weight: 400;
  margin: 0;
  padding: 0;
}
h1 {
  text-transform: uppercase;
}
nav {
  font-weight: 300;
  margin-top: auto;
  margin-bottom: auto;
}
nav ul {
  display: -webkit-flex;
  display: flex;
  flex-direction: row;
  list-style: none;
  margin: 0;
  padding: 0;
}
.container {
  margin: auto 20px;
}
@media (min-width: 768px) {
  .container {
    margin: auto;
    width: 720px;
  }
}
@media (min-width: 992px) {
  .container {
    margin: auto;
    width: 960px;
  }
}
@media (min-width: 1280px) {
  .container {
    margin: auto;
    width: 1200px;
  }
}
.page-header {
  background-color: #34495e;
}
.page-header .additional-header {
  background-color: #2c3e50;
}
.page-header .additional-header li {
  margin-left: 23px;
}
.page-header .additional-header .container {
  display: -webkit-flex;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  height: 40px;
}
.page-header .additional-header .container a {
  color: #bbc5d0;
}
@media (min-width: 768px) {
  .page-header .main-header ul {
    margin-left: 62.5px;
  }
}
@media (min-width: 992px) {
  .page-header .main-header ul {
    margin-left: 102.5px;
  }
}
@media (min-width: 1280px) {
  .page-header .main-header ul {
    margin-left: 142.5px;
  }
}
.page-header .main-header li {
  margin-left: 26px;
}
@media (min-width: 768px) {
  .page-header .main-header li {
    margin-right: 26px;
  }
}
.page-header .main-header .container {
  display: -webkit-flex;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  height: 51px;
}
.page-header .main-header .container .logo {
  font-size: 24pt;
  margin-top: auto;
  margin-bottom: auto;
}
.page-header .main-header .container a {
  color: #ffffff;
}
@media (min-width: 768px) {
  .page-header .main-header .container {
    justify-content: flex-start;
  }
}
.page-title {
  margin: 40px 0 30px 0;
}
.page-content {
  display: -webkit-flex;
  display: flex;
  flex-direction: row;
  flex: auto;
}
.page-footer {
  display: -webkit-flex;
  display: flex;
  flex-direction: row;
  background-color: #34495e;
  color: #ffffff;
  font-weight: 300;
  height: 72px;
}
.page-footer a {
  color: #ffffff;
}
.page-footer .container {
  margin: auto;
}
#main-content {
  display: flex;
  flex-grow: 2;
  flex-direction: column;
}
@media (min-width: 768px) {
  #main-content {
    width: 460px;
  }
}
@media (min-width: 992px) {
  #main-content {
    width: 620px;
    margin-right: 10px;
  }
}
@media (min-width: 1280px) {
  #main-content {
    width: 780px;
    margin-right: 10px;
  }
}
#sidebar {
  flex-grow: 1;
  flex-direction: column;
  justify-content: flex-start;
  margin-left: 10px;
  display: none;
}
@media (min-width: 992px) {
  #sidebar {
    display: flex;
    width: 300px;
  }
}
@media (min-width: 1280px) {
  #sidebar {
    display: flex;
    width: 380px;
  }
}



