#latest-news {
  display: flex;
  align-items: flex-start;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0;
  width: auto;
}
#top-news {
  background-color: #ffffff;
  margin: 0;
  width: auto;
  padding: 19px 23px;
}
.article {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  background-color: #ffffff;
  box-shadow: 0 2px 0 0 #DEE2E5, inset 0 -40px 0 0 #EAEEF1;
  margin-bottom: 32px;
  width: 100%;
}
.article footer {
  background-color: #ebeef0;
  font-size: 12pt;
  padding: 19px;
}
.article footer p {
  margin: 0;
}
@media (min-width: 992px) {
  .article {
    width: 300px;
  }
}
@media (min-width: 1280px) {
  .article {
    width: 380px;
  }
}
.article .article-image {
  order: 1;
}
.article .article-heading {
  order: 2;
}
.article .article-description {
  order: 3;
}
.article .article-footer {
  order: 4;
}
.article-title {
  font-size: 18pt;
  padding: 19px 19px 0 19px;
}
.article-title a {
  color: #6a7884 !important;
}
.article-image {
  display: flex;
}
.article-image img {
  height: 100%;
  width: 100%;
}
@media (min-width: 992px) {
  .article-image img {
    width: 300px;
  }
}
@media (min-width: 1280px) {
  .article-image img {
    width: 380px;
  }
}
.article-description {
  padding: 19px;
}
.article-aside {
  display: flex;
  margin-bottom: 15px;
}
.article-aside .article-heading {
  order: 2;
  margin-left: 10px;
}
.article-aside .article-heading h2 {
  font-size: 14pt;
  font-family: 'Ubuntu', sans-serif;
  padding: 0;
}
.article-aside .article-body img {
  height: 70px;
  width: auto;
}



